import { inspect } from 'util';

/**
 * Check an object for null/undefined and empty (no-key)
 * @param obj
 */
export const isObject = (obj: object): boolean => {
    return obj instanceof Object && Object.prototype.toString.call(obj) === '[object Object]';
};

/**
 * Check an object for null/undefined and empty (no-key)
 * @param obj
 */
export const isEmptyOrNullObject = (obj: object | null): boolean => {
    return !obj || Object.keys(obj).length <= 0;
};

/**
 * Safe parse json or throw exception
 * @param content
 */
export const safeParsePayload = (content: string, trace?: { error: Function }) => {
    try {
        return content.constructor === String ? JSON.parse(content) : content;
    } catch (error) {
        trace = trace || console;
        trace.error(`JSON Parsing fails for content -- ${content}`);
        trace.error(inspect(error));
        return undefined;
    }
};
